body {
    font-family: Arial, sans-serif;
    line-height: 1.6;
    margin: 20px;
}

h1, h2, h3, h4, h5, h6 {
    color: #333;
}

p {
    margin: 10px 0;
}

ul, ol {
    margin: 10px 0 10px 20px;
}

code {
    background-color: #f4f4f4;
    padding: 2px 4px;
    border-radius: 4px;
}

blockquote {
    border-left: 4px solid #ccc;
    padding-left: 10px;
    color: #555;
    margin: 10px 0;
}

a {
    color: #007bff;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}